<template lang="html">
    <div class="container-fluid gwendolyn">
      <h2>Gwendolyn the Rabbit</h2>
      <div class="container-fluid">
          <h4>What is it?</h4>
          <p><a href="http://gwendolyn.win">Gwendolyn the Rabbit</a> is an infinite scroller game featuring Adam's neice in the form of a rabbit, jumping on ledges, stomping on snowmen, and eating carrots.</p>
          <h4>What technologies does it use?</h4>
          <p>Built using Phaser.io, it is a JavaScript based HTML5 game which uses WebGL or Canvas depending on browser compatablility.</p>
          <h4>Why is it a thing?</h4>
          <p>It was a fun way to play around with a new JavaScript library, and made a great Christmas present for a neice who is constantly jumping around.</p>
      </div>
    </div>
</template>

<script>
  export default {
    name: 'Gwendolyn'
  }
</script>

<style lang="css">
</style>



